import React, { useState, useEffect } from 'react';
import axios from 'axios';

export default function InventoryAdd() {

    const [title, setTitle] = useState("")
    const [inventory_description, setInventory_description] = useState("")
    const [serial_number, setSerial_number] = useState("")
    const [price, setPrice] = useState("")
    const [date_purchase, setDate_purchase] = useState("")
    const [date_update, setDate_update] = useState("")
    const [category, setCategory] = useState("")
    const [owner, setOwner] = useState("")

    const [icategory, setIcategory] = useState([])
    const [iowner, setIowner] = useState([])

    useEffect(() =>{
        axios({
        method: "GET",
        url: "http://127.0.0.1:8000/api/v1/owner/"
        }).then(response => {
        setIowner(response.data)
        })
    }, [])


    useEffect(() =>{
        axios({
        method: "GET",
        url: "http://127.0.0.1:8000/api/v1/category/"
        }).then(response => {
        setIcategory(response.data)
        })
    }, [])

    const AddNewInventory = async (e) => {
        e.preventDefault();

        let formField = new FormData()

        formField.append('title', title)
        formField.append('inventory_description', inventory_description)
        formField.append('serial_number', serial_number)
        formField.append('price', price)
        formField.append('date_purchase', date_purchase)
        formField.append('category', category)
        formField.append('owner', owner)


    function readCookie(name) {
        let nameEQ = name + "=";
        let ca = document.cookie.split(';');
        for(let i=0;i < ca.length;i++) {
            let c = ca[i];
            while (c.charAt(0)===' ') c = c.substring(1,c.length);
            if (c.indexOf(nameEQ) === 0) return c.substring(nameEQ.length,c.length);
        }
        return null;
    }

    let csrftoken = readCookie('csrftoken');


    if (title && price && serial_number) {
        await axios({
            headers: {"X-CSRFToken":csrftoken },
            method: 'post',
            url: 'http://127.0.0.1:8000/api/v1/inventory/',
            data: formField
        }).then(response => {
            alert(`Inventory ${title} has been added`);
            console.log(formField);
            })
        } else {
            alert('Title, Serial number & Price must be added');
        }
    }

    return (
        <div className="container sm">
            <h3 className="text-center">Add new Inventory</h3>
            <form className="form-control p-2" onSubmit={AddNewInventory}>

                <select className="form-select mb-2" name="category" onSelect={(e) => setCategory(e.target.value)}>
                  <option value="">Open this select menu</option>
                  {icategory.map(c => (
                  <option key={c.id} value={c.id}>{c.category_title}</option>
                  ))}
                </select>

                <select className="form-select" name="owner" onSelect={(e) => setOwner(e.target.value)}>
                  <option value="">Open this select menu</option>
                  {iowner.map(o => (
                  <option key={o.id} value={o.id}>{o.surname} {o.name[0]}.</option>
                  ))}
                </select>

                <input
                    type="text"
                    className="form-control my-2"
                    placeholder="Title"
                    name="title"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    />
                <input
                    type="text"
                    className="form-control mb-2"
                    placeholder="Serial number"
                    name="serial_number"
                    value={serial_number}
                    onChange={(e) => setSerial_number(e.target.value)}
                    />
                <input
                    type="text"
                    className="form-control mb-2"
                    placeholder="Description"
                    name="inventory_description"
                    value={inventory_description}
                    onChange={(e) => setInventory_description(e.target.value)}
                    />
                <input
                    type="text"
                    className="form-control mb-2"
                    placeholder="Price"
                    name="price"
                    value={price}
                    onChange={(e) => setPrice(e.target.value)}
                    />
                <input
                    type="date"
                    className="form-control mb-2"
                    placeholder="Date of purchase"
                    name="date_purchase"
                    value={date_purchase}
                    onChange={(e) => setDate_purchase(e.target.value)}
                    />

                <button type="submit" className="btn btn-secondary my-2">Submit</button>
            </form>
        </div>
    )
}
